import React, { useContext, useEffect, useRef, useState } from "react";
import User from "../images/user-2.png";
import "../style/Pbox.css";
import { userContext } from "./StateManagement";
import Axios from "axios";

const PChatBox = (props) => {
  const State = useContext(userContext);
  const { data, dispatch } = State;
  const { user, photoUrl, posts, friends, loggedIn, googleId } = data;
  const { fid, fphotoUrl, fname } = props.user;
  const removeFriend = props.method;
  const scroll = useRef();
  const [hidePbox, setToggle] = useState(true);

  const [message, setMessage] = useState("");
  const [msgs, setMsgs] = useState([]);

  useEffect(() => {
    if (fname) {
      setToggle(false);
    }
    fetchMessages();
    return () => {
      setMsgs([]);
    };
  }, [fname]);

  useEffect(() => {
    // fetchMessages();
  });

  const set = () => {
    removeFriend();
    setToggle(true); //hide
  };

  const fetchMessages = () => {
    let obj = {
      uid: googleId,
      fid: fid,
    };
    Axios.post("http://localhost:3001/api/chat/fetchMessages", obj).then(
      (response) => {
        console.log(response);
        setMsgs(response.data.reverse());
        scroll.current.scrollIntoView({ behavior: "smooth" });
      }
    );
  };

  const handleMessage = (e) => {
    console.log(message);
    let obj = {
      text: message,
      uid: googleId,
      purl: photoUrl,
      fpurl: fphotoUrl,
      fid: fid,
    };
    Axios.post("http://localhost:3001/api/send", obj).then((response) => {
      console.log(response);
      fetchMessages();
    });
    scroll.current.scrollIntoView({ behavior: "smooth" });

    setMessage("");
  };
  return (
    <div
      className={hidePbox ? "portable_chatBox hide-Pbox" : "portable_chatBox"}
    >
      <div className="user-details">
        <div>
          <img
            className="small-profile-pic"
            src={fphotoUrl}
            width="50px"
            alt="profile pic"
          ></img>
          <h3 className="user_name">{fname}</h3>
        </div>
        <div className="close-portable_chatBox">
          <button onClick={set} id="close_pcb" className="close">
            &#x292B;
          </button>
        </div>
      </div>

      <div className="portable_chatBox-messages">
        {/* messages  */}
        {msgs.length == 0 ? (
          <div>empty</div>
        ) : (
          <React.Fragment>
            {msgs.map((item) => {
              if (item.sender == googleId) {
                return (
                  <div className="sender">
                    <div className="message">
                      <p>{item.message}</p>
                    </div>
                    <div className="messages-profile">
                      <img src={item.photoUrl} width="30px"></img>
                    </div>
                  </div>
                );
              } else {
                return (
                  <div className="receiver">
                    <div className="messages-profile">
                      <img src={fphotoUrl} width="30px"></img>
                    </div>
                    <div className="message">
                      <p>{item.message}</p>
                    </div>
                  </div>
                );
              }
            })}
          </React.Fragment>
        )}
        <div ref={scroll}></div>
      </div>

      <div className="portable_chatBox-send-message">
        <div>
          <input
            type="text"
            placeholder="Type a message..."
            onChange={(e) => setMessage(e.target.value)}
            value={message}
          ></input>
        </div>
        <div>
          <button
            className="send-message-btn"
            onClick={handleMessage}
            disabled={!message}
          >
            Send
          </button>
        </div>
      </div>
    </div>
  );
};

export default PChatBox;
