import React, { useContext, useEffect, useState } from "react";
import Navigation from "./Navigation";
import Posts from "./Home-post";
import ActiveUsers from "./home-activeUsers";
import { userContext } from "./StateManagement";
import { auth } from "./firebase";
import { v4 as uuidv4 } from "uuid";
import Axios from "axios";

import "../style/home.css";
import "../style/home-mediaQueries.css";
import User from "../images/user-2.png";
import { useHistory } from "react-router";

const Home = () => {
  const State = useContext(userContext);
  const { data, dispatch } = State;
  const { user, photoUrl, posts, friends, loggedIn, googleId } = data;
  const [blog, setBlog] = useState("");

  const history = useHistory();

  //user details

  useEffect(() => {
    const unSubscribed = auth.onAuthStateChanged((authUser) => {
      if (authUser) {
        console.log("fetching...");
        let obj = {
          id: authUser.uid,
        };
        Axios.all([
          Axios.post("http://localhost:3001/api/profile", obj),
          Axios.post("http://localhost:3001/api/fetchPosts", obj),
        ]).then(async (responseArr) => {
          // console.log(responseArr[0].data);
          await updateState(responseArr[0].data);
          // console.log(responseArr[1]);
          await updatePost(responseArr[1].data);
        });
      } else {
        history.push("/login");
      }
      return () => {
        unSubscribed();
      };
    });
  }, []);

  const updateState = (data) => {
    let { name, googleId, photoUrl, loggedIn } = data;
    dispatch({
      type: "details",
      user: name,
      gid: googleId,
      purl: photoUrl,
      loggedIn: loggedIn,
    });
  };

  const updatePost = (posts) => {
    dispatch({
      type: "posts",
      newpost: posts,
    });
  };

  const handleCreatePost = (e) => {
    e.preventDefault();
    console.log("clicked");
    let post = {
      text: blog,
      id: uuidv4(),
      uid: googleId,
    };
    Axios.post("http://localhost:3001/api/addPost", post)
      .then(async (result) => {
        setBlog("");
        let newposts = [...posts];
        newposts.unshift(result.data);
        await updatePost(newposts);
      })
      .catch((err) => console.log(err));
  };

  // console.log(State);

  return (
    <div className="home">
      <div className="blank">
        <Navigation className="home-navigation" />
      </div>
      <div className="home-blog">
        <div className="home-postBlog">
          <div className="postBlog-profilePic">
            <img
              src={photoUrl}
              className="home-small-profilePic"
              width="40px"
              alt="Profile Pic"
            ></img>
          </div>
          <div className="postBlog-content">
            <form onSubmit={handleCreatePost}>
              <div className="blog">
                <textarea
                  placeholder="What's happening ?"
                  value={blog}
                  onChange={(e) => setBlog(e.target.value)}
                ></textarea>
              </div>
              <div className="postBlog-post">
                <button type="submit" className="postBlog-postBtn" disabled={!blog}>
                  Post
                </button>
              </div>
            </form>
          </div>
        </div>
        {/* posts  */}
        {posts?.length != 0 ? (
          <React.Fragment>
            {posts.map((post) => (
              <Posts key={post._id} value={{ name: user, photoUrl: photoUrl, post: post, likes: post.likes}} />
            ))}
          </React.Fragment>
        ) : (
          <div>no</div>
        )}
      </div>
      <div className="home-activeUsers">
        <ActiveUsers />
      </div>
    </div>
  );
};

export default Home;
