import './App.css';
// import "./style/app.css";
import Header from "./component/Header";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import Profile from "./component/Home-Profile";
import Home from "./component/Home"
import Chat from "./component/chat/chat";
import Rfriend from "./component/Friends/RecommedFriend";
import LogIn from "./component/Login";
import { userContext, initialValue, Reducer} from "./component/StateManagement";
import { useEffect, useReducer, useState } from "react";

function App() {
  const [Obj, dispatch] = useReducer(Reducer, initialValue);
  return (
    <userContext.Provider value={ {data: Obj, dispatch: dispatch}}>
    <div className="App">
      <Router>
        <Switch>
          <Route path="/home">
            <Header />
            <Home />
          </Route>
          <Route path="/login">
            {/* <Header /> */}
            <LogIn />
          </Route>
          <Route path="/profile">
          <Header />
            <Profile />
          </Route>
          <Route path="/chat">
            <Header />
            <Chat />
          </Route>
          <Route path="/friends">
            <Header />
            <Rfriend />
          </Route>
        </Switch>
      </Router>
    </div>
    </userContext.Provider>
  );
}

export default App;
