import React, { useContext, useEffect, useState } from "react";
import User from "../images/user-2.png";
import "../style/activeUser.css";
import PBox from "./Home-portableChatBox";
import Axios from "axios";
import { auth } from "./firebase";

import { userContext } from "./StateManagement";

const ActiveUsers = () => {
  const State = useContext(userContext);
  const { data, dispatch } = State;
  const { googleId } = data;

  const [friend, setFriend] = useState({ id: 0, name: null });
  const [friendsList, setList] = useState([]);

  const friendDetails = (detail) => {
    setFriend(detail);
  };
  const removeFriend = () => {
    setFriend({ id: 0, name: null });
  };

  useEffect(() => {
    const unSubscribed = auth.onAuthStateChanged((authUser) => {
      if (authUser) {
        fetchFriends(authUser.uid);
      }
    });
  }, []);

  const fetchFriends = (id) => {
    let obj = {
      id: id,
    };
    Axios.post("http://localhost:3001/api/fetchFriends", obj).then(
      (response) => {
        setList(response.data);
      }
    );
  };

  const updateFriends = (friends) => {
    dispatch({
      type: "friends",
      newfriends: friends,
    });
  };

  return (
    <div>
      <div className="list">
        {friendsList.length == 0 ? (
          <div>No Friends</div>
        ) : (
          <React.Fragment>
            <ul id="friends_list">
              {friendsList.map((friend) => (
                <li
                  onClick={() => setFriend(friend)}
                  key={friend._id}
                  className="friends"
                >
                  <img
                    className="home-small-profilePic"
                    src={friend.fphotoUrl}
                    width="50px"
                  ></img>
                  {friend.fname}
                </li>
              ))}
            </ul>
          </React.Fragment>
        )}
      </div>
      <PBox user={friend} method={removeFriend} />
    </div>
  );
};

export default ActiveUsers;
