import React, { useContext, useEffect, useState } from "react";
import { useHistory } from "react-router";
import Axios from "axios";
import LOGO from "../images/logo.png";
import LOGOIMAGE from "../images/my_logo.png";
import "../style/login.css";

import { provider, auth } from "./firebase";
import { userContext } from "./StateManagement";

const LogIn = () => {
  const history = useHistory();
  const State = useContext(userContext);
  const { data, dispatch } = State;

  const [signIn, setSign] = useState(false);
  //   const [userName, setUser] = useState("");

  useEffect(() => {
    const unSubscribed = auth.onAuthStateChanged((authUser) => {
      if (authUser) {
        async function update() {
          await updateData(
            authUser.displayName,
            authUser.photoURL,
            authUser.uid
          );
          history.push("/home");
        }
        update();
      } else {
        // console.log("logout process....");
      }
      return () => {
        unSubscribed();
      };
    });
  }, []);

  const handleGoogleSignIn = () => {
    auth.signInWithPopup(provider).then((result) => {
      setSign(true);
      let userName = result.user.displayName;
      let photoUrl = result.user.photoURL;
      let uid = result.user.uid;
      let obj = {
        googleId: result.user.uid,
        name: userName,
        url: photoUrl,
      };

      Axios.post("http://localhost:3001/api/addProfile", obj)
        .then(async (result) => {
          // console.log(result.data);
          await updateState(result.data);
          history.push("/home");
        })
        .catch((err) => console.log(err));
    });
  };

  const updateData = (name, photoUrl, id) => {
    setSign(true);
    dispatch({
      type: "login",
      user_name: name,
      login: true,
      url: photoUrl,
      uid: id,
    });
    return;
  };

  const updateState = (data) => {
    const { name, googleId, photoUrl, loggedIn } = data;
    dispatch({
      type: "details",
      user: name,
      gid: googleId,
      purl: photoUrl,
      loggedIn: loggedIn,
    });
  };

  const handleGoogleSignOut = () => {
    auth.signOut();
    setSign(false);
  };

  return (
    <div className="container">
      <div className="theme">
        <div>
          <div className="logo">
            <img src={LOGOIMAGE} alt="Logo" width="150px"></img>
            <div className="title">Chatbook</div>
          </div>
        </div>
      </div>
      <div className="functions">
        {signIn ? (
          <div>
            <button className="bubbly-button" onClick={handleGoogleSignOut}>
              Sign out
            </button>
          </div>
        ) : (
          <div>
            <button className="bubbly-button" onClick={handleGoogleSignIn}>
              Sign in with Google
            </button>
          </div>
        )}
      </div>
    </div>
  );
};

export default LogIn;
