import React, { useContext, useEffect, useState } from "react";
import Navigation from "../Navigation";
import { v4 as uuidv4 } from "uuid";
import Axios from "axios";
import { auth } from "../firebase";

import "../../style/friends/RecommendF.css";
import { userContext } from "../StateManagement";
import { useHistory } from "react-router";

const RecommendFriend = () => {
  const [profiles, setProfiles] = useState([]);
  const [reqs, setReqs] = useState([]);
  const State = useContext(userContext);
  const { data, dispatch } = State;
  const { user, googleId, photoUrl, friends, loggedIn } = data;
  const history = useHistory();
  const [alert, setAlert] = useState("");

  useEffect(() => {
    const unSubscribed = auth.onAuthStateChanged((authUser) => {
      if (!authUser) history.push("/login");
    });
    console.log("mounted");
    fetchProfiles();
    fetchRequests();
    return () => {
      unSubscribed();
    };
  }, []);

  const fetchProfiles = () => {
    Axios.get("http://localhost:3001/api/profiles").then((response) => {
      //   console.log(response);
      setProfiles(response.data);
    });
  };
  //   console.log(profiles);

  const fetchRequests = () => {
    let obj = {
      id: googleId,
    };
    Axios.post("http://localhost:3001/api/fetchRequests", obj).then(
      (response) => {
        // console.log(response);
        setReqs(response.data);
      }
    );
  };

  const handleAccept = (fdata) => {
    console.log(fdata._id);
    let obj = {
      name: user,
      uid: googleId,
      purl: photoUrl,
      fpurl: fdata.photoUrl,
      fid: fdata.googleId,
      fname: fdata.name,
      did: fdata._id,
    };
    Axios.post("http://localhost:3001/api/accept", obj).then((response) => {
      //   console.log(response);
      fetchRequests();
      setAlert("You are friends now.");
    });
  };

  const handleRequest = (fdata) => {
    let obj = {
      name: user,
      uid: googleId,
      purl: photoUrl,
      fpurl: fdata.photoUrl,
      fid: fdata.googleId,
      fname: fdata.name,
    };

    Axios.post("http://localhost:3001/api/addFriend", obj).then((response) => {
      //   console.log(response);
      setAlert("You have sent the friend request.");
    });

    // console.log(obj);
  };

  const handleAlert = () => {
    console.log("closed");
  }

  return (
    <div className="friends">
      <div>
        <Navigation />
      </div>
      <div className="friends-list">
        <div className={(alert?.length == 0 ? "alert hide-alert" : "alert")} onClick={() => setAlert("")}>{alert}</div>
        <div className="friend-requests">
          <h2>Friend Requests</h2>
          <ul>
            {reqs.length == 0 ? (
              <div>No Requests</div>
            ) : (
              <React.Fragment>
                {reqs.map((item) => (
                  <li>
                    <div className="user-container">
                      <div className="image">
                        <img src={item.photoUrl} alt="image"></img>
                      </div>
                      <div className="user-details">
                        <p>{item.name}</p>
                        <button
                          className="follow-btn"
                          onClick={() => handleAccept(item)}
                        >
                          Accept
                        </button>
                      </div>
                    </div>
                  </li>
                ))}
              </React.Fragment>
            )}
          </ul>
        </div>
        <div>
          <h2>Suggested People</h2>
          <ul>
            {profiles.length == 0 ? (
              <div>Empty</div>
            ) : (
              <React.Fragment>
                {profiles.map((item) => (
                  <li>
                    <div className="user-container">
                      <div className="image">
                        <img src={item.photoUrl} alt="image"></img>
                      </div>
                      <div className="user-details">
                        <p>{item.name}</p>
                        <button
                          className="follow-btn"
                          onClick={() => handleRequest(item)}
                        >
                          Follow
                        </button>
                      </div>
                    </div>
                  </li>
                ))}
              </React.Fragment>
            )}
          </ul>
        </div>
      </div>
    </div>
  );
};

export default RecommendFriend;
