import React, { useContext, useEffect, useRef, useState } from "react";
import User from "../../images/user-2.png";
import "../../style/chat/chatbox.css";
import "../../style/chat/chat-mediaQueries.css";
import Axios from "axios";
import { userContext } from "../StateManagement";

const ChatBox = (props) => {
  const State = useContext(userContext);
  const { data, dispatch } = State;
  const scroll = useRef();
  const { user, photoUrl, posts, friends, loggedIn, googleId } = data;
  const { fid, fphotoUrl, fname } = props.user;
  const [msgs, setMsgs] = useState([]);
  const [message, setMessage] = useState("");

  // console.log(props);

  useEffect(() => {
    if (fname) {
      fetchMessages();
    }
    return () => {
      setMsgs([]);
    };
  }, [fname]);

  useEffect(() => {
    // fetchMessages();
  });

  const fetchMessages = () => {
    let obj = {
      uid: googleId,
      fid: fid,
    };
    Axios.post("http://localhost:3001/api/chat/fetchMessages", obj).then(
      (response) => {
        console.log(response);
        setMsgs(response.data.reverse());
        scroll.current.scrollIntoView({ behavior: "smooth" });
      }
    );
  };

  const handleMessage = (e) => {
    console.log(message);
    let obj = {
      text: message,
      uid: googleId,
      purl: photoUrl,
      fpurl: fphotoUrl,
      fid: fid,
    };
    Axios.post("http://localhost:3001/api/send", obj).then((response) => {
      console.log(response);
      fetchMessages();
    });
    scroll.current.scrollIntoView({ behavior: "smooth" });

    setMessage("");
  };

  return (
    <div className="chat-chatbox">
      {/* 1 */}
      <div className="user-details">
        <div>
          <img
            className="small-profile-pic"
            src={User}
            width="50px"
            alt="profile pic"
          ></img>
          <h3 className="user_name">{fname}</h3>
        </div>
        <div className="close-portable_chatBox">
          <button id="close_pcb" className="close">
            &#x292B;
          </button>
        </div>
      </div>
      {/* 2  */}
      <div className="chat_chatBox-messages">
        {msgs.length == 0 ? (
          <div>empty</div>
        ) : (
          <React.Fragment>
            {msgs.map((item) => {
              if (item.sender == googleId) {
                return (
                  <div className="sender">
                    <div className="message">
                      <p>{item.message}</p>
                    </div>
                    <div className="messages-profile">
                      <img src={item.photoUrl} width="30px"></img>
                    </div>
                  </div>
                );
              } else {
                return (
                  <div className="receiver">
                    <div className="messages-profile">
                      <img src={fphotoUrl} width="30px"></img>
                    </div>
                    <div className="message">
                      <p>{item.message}</p>
                    </div>
                  </div>
                );
              }
            })}
          </React.Fragment>
        )}
        <div ref={scroll}></div>
      </div>
      {/* 3 */}
      <div className="chat_chatBox-send-message">
        <div className="chat-input-control">
          <input
            type="text"
            placeholder="Type a message..."
            value={message}
            onChange={(e) => setMessage(e.target.value)}
          ></input>
        </div>
        <div className="chat-send-control">
          <button
            className="send-message-btn"
            onClick={handleMessage}
            disabled={!message}
          >
            Send
          </button>
        </div>
      </div>
    </div>
  );
};

export default ChatBox;
