import React, { useContext, useState } from "react";
import Logo from "../images/logo.png";
import User from "../images/user-2.png";
import "../style/navigation.css";
import { Link } from "react-router-dom";
import FacebookIcon from "@material-ui/icons/Facebook";
import InstagramIcon from "@material-ui/icons/Instagram";
import WhatsAppIcon from "@material-ui/icons/WhatsApp";
import TwitterIcon from "@material-ui/icons/Twitter";
import MenuIcon from "@material-ui/icons/Menu";
import { userContext } from "./StateManagement";
import { useHistory } from "react-router";
import { auth } from "./firebase";

const Navigation = () => {
  const State = useContext(userContext);
  const data = State.data;
  const { user, photoUrl, posts, friends, loggedIn, googleId } = data;
  const history = useHistory();

  const [toggleNavigation, setToggleN] = useState(false);
  const toggleMenu = () => {
    if (toggleNavigation == true) setToggleN(false);
    else setToggleN(true);
  };
  const redirectToProfile = () => {
    history.push({
      pathname: "/profile",
      search: "?id=" + googleId,
    });
  };

  const handleSignOut = () => {
      auth.signOut();
      history.push("/login");
  }

  return (
    <div>
      <div className="menuIcon">
        <MenuIcon onClick={toggleMenu}></MenuIcon>
      </div>
      <div className="navigation-overlay ">
        <div
          className={
            toggleNavigation
              ? "navigation-sidebar"
              : "navigation-sidebar hide-navigation-sidebar"
          }
        >
          <nav>
            <div className="navigation-logo">
              <img src={Logo} width="110px" alt="Logo"></img>
              <button
                onClick={() => setToggleN(false)}
                id="close_nav"
                className="navigation-close"
              >
                &#x292B;
              </button>
            </div>
            <div className="navigation-main_profile">
              <img src={photoUrl} width="160px"></img>
              <h2>{user}</h2>
            </div>
            <ul id="navigation-menuitems">
              <li onClick={redirectToProfile}>
                {/* <Link to="/profile" className="link"> */}
                Profile
                {/* </Link> */}
              </li>
                <Link to="/chat" className="link">
              <li>
                  Chat
              </li>
                </Link>
                <Link to="/friends" className="link">
              <li>
                  Friends
              </li>
                </Link>
              <li onClick={handleSignOut}>
                  Sign out
              </li>
            </ul>
            <div className="social">
              <FacebookIcon className="social-icon"></FacebookIcon>
              <InstagramIcon className="social-icon"></InstagramIcon>
              <TwitterIcon className="social-icon"></TwitterIcon>
              <WhatsAppIcon className="social-icon"></WhatsAppIcon>
            </div>
          </nav>
        </div>
      </div>
    </div>
  );
};

export default Navigation;
