import React, { useContext, useEffect, useState } from "react";
import { useHistory } from "react-router";
import Navigation from "./Navigation";
import Axios from "axios";
import { auth } from "./firebase";

import Posts from "./Home-post";

import { userContext } from "./StateManagement";
import "../style/home-profile.css";
import "../style/home.css";

const Profile = () => {
  const State = useContext(userContext);
  const { data, dispatch } = State;
  const { user, otherUser, otherUserPUrl, otherUserPosts } = data;
  const history = useHistory();
  if (user == "") {
    history.push("/home");
  }

  //profile details

  useEffect(() => {
    const unSubscribed = auth.onAuthStateChanged((authUser) => {
      if (!authUser) history.push("/login");
    });

    const url = new URL(window.location.href);
    const gid = url.searchParams.get("id");
    let obj = {
      id: gid,
    };
    Axios.all([
      Axios.post("http://localhost:3001/api/profile", obj),
      Axios.post("http://localhost:3001/api/fetchPosts", obj),
    ]).then(async (responseArr) => {
      // console.log(responseArr[0].data);
      await updateState(responseArr[0].data);
      // console.log(responseArr[1]);
      await updatePost(responseArr[1].data);
    });
    return () => {
      unSubscribed();
    };
  }, []);

  const updateState = (data) => {
    const { name, googleId, photoUrl, loggedIn } = data;
    dispatch({
      type: "otherUserDetails",
      user: name,
      purl: photoUrl,
    });
  };

  const updatePost = (posts) => {
    dispatch({
      type: "otherUserPosts",
      newpost: posts,
    });
  };

  return (
    <div className="profile">
      <div className="blank">
        <Navigation className="home-navigation" />
      </div>
      <div className="profile-main">
        <div className="profile-image">
          <img src={otherUserPUrl} width="160px"></img>
        </div>
        <h2>{otherUser}</h2>
      </div>
      {otherUserPosts?.length != 0 ? (
        <React.Fragment>
          {otherUserPosts.map((post) => (
            <Posts
              key={post._id}
              value={{
                name: otherUser,
                photoUrl: otherUserPUrl,
                post: post,
                likes: post.likes,
              }}
            />
          ))}
        </React.Fragment>
      ) : (
        <div>no</div>
      )}
    </div>
  );
};

export default Profile;
