import React, { useContext, useEffect, useState } from "react";
import User from "../images/user-2.png";
import ThumbUpAltIcon from "@material-ui/icons/ThumbUpAlt";
import CommentIcon from "@material-ui/icons/Comment";
import ShareIcon from "@material-ui/icons/Share";
import { userContext } from "./StateManagement";
import Axios from "axios";

const Post = (props) => {
  const State = useContext(userContext);
  const { data, dispatch } = State;
  const { user, friends, loggedIn, googleId } = data;
  const { post, name, photoUrl, likes } = props.value;

  const [Like, toggleLike] = useState(false);
  const [totalLikes, setLikes] = useState(0);
  // console.log(totalLikes);

  const handleLikes = async (id, likes) => {
    if (!Like) {
      updateLikes(id, totalLikes + 1);
      await likeFunction(totalLikes + 1);
      toggleLike(true);
    } else {
      updateLikes(id, totalLikes - 1);
      await likeFunction(totalLikes - 1);
      toggleLike(false);
    }
  };

  const likeFunction = async (likes) => {
    setLikes(likes);
  };

  useEffect(() => {
    setLikes(likes);
  }, []);

  const updateLikes = (id, newlikes) => {
    let obj = {
      id: id,
      likes: newlikes,
    };
    Axios.post("http://localhost:3001/api/setlikes", obj).then((response) =>
      console.log(response)
    );
  };
  console.log(totalLikes);
  return (
    <div className="home-posts">
      <div className="home-post" key={post?.pid}>
        <div className="post-header">
          <img
            className="home-small-profilePic"
            src={photoUrl}
            width="40px"
          ></img>{" "}
          <div>
            <div className="post-userName">{name}</div>
            <p className="post-date">{post?.date}</p>
          </div>
        </div>
        <div className="blog-text">
          <p>{post?.blog}</p>
        </div>
        <div className="post-likes">
          {totalLikes <= 0 ? "" : `${totalLikes}`}
        </div>
        <div className="post-btns">
          <button onClick={() => handleLikes(post?._id, totalLikes)}>
            <ThumbUpAltIcon className="post-likeShare"></ThumbUpAltIcon> Like
          </button>
          <button>
            <CommentIcon className="post-likeShare"></CommentIcon> Comment
          </button>
          <button>
            <ShareIcon className="post-likeShare"></ShareIcon> Share
          </button>
        </div>
      </div>
    </div>
  );
};

export default Post;
