import React, { useState, useContext, useEffect } from "react";
import User from "../../images/user-2.png";
import "../../style/chat/users.css";
import { userContext } from "../StateManagement";
import ChatBox from "./chatbox";

import Axios from "axios";

const ActiveUsers = () => {
  const [friendsList, setList] = useState([]);
  const [friend, setFriend] = useState({ id: 0, name: null });

  const State = useContext(userContext);
  const { data, dispatch } = State;
  const { user, photoUrl, posts, friends, loggedIn, googleId } = data;

  //user details

  useEffect(() => {
    fetchFriends();
  }, []);

  const fetchFriends = () => {
    let obj = {
      id: googleId,
    };
    Axios.post("http://localhost:3001/api/fetchFriends", obj).then(
      (response) => {
        // console.log(response);
        setList(response.data);
      }
    );
  };
  return (
    <div className="chat-list-box">
      <div className="users">
        <ul className="users-friends-list">
          {friendsList.map((friend) => (
            <li
              key={friend._id}
              className="friends"
              onClick={() => setFriend(friend)}
            >
              <img
                className="chat-small-profilePic"
                src={friend.fphotoUrl}
                width="50px"
              ></img>
              {friend.fname}
            </li>
          ))}
        </ul>
      </div>
      <div className="chat-module">
        <ChatBox user={friend} />
      </div>
    </div>
  );
};

export default ActiveUsers;
