import React from "react";

export const userContext = React.createContext();

export const initialValue = {
  user: "",
  otherUser: "",
  otherUserPosts: [],
  otherUserPUrl: "",
  googleId: "",
  photoUrl: "",
  friends: [],
  posts : [],
  loggedIn: false
};



export const Reducer = (state, action) => {
  switch (action.type) {
    case "details":
        return { ...state, 
            user: action.user,
            googleId: action.gid,
            photoUrl: action.purl,
            loggedIn: action.loggedIn
        };
    case "login":
      return { ...state, 
        user: action.user_name, 
        loggedIn: action.login, 
        photoUrl: action.url, 
        googleId: action.uid
     };
    case "posts":
        let newposts = action.newpost;
        return { ...state, posts: newposts};
    case "friends":
        let f = action.newfriends;
        return { ...state, friends: f};
    case "otherUserDetails":
        return { ...state,
            otherUser: action.user,
            otherUserPUrl: action.purl,
        };
    case "otherUserPosts":
        let otherposts = action.newpost;
        return { ...state, otherUserPosts: otherposts};

  }
};


